.st-final-cost__price { font-weight: 700; font-size: 27px; opacity: 0.8; line-height: 1; text-transform: uppercase; }

@media only screen and (max-width: 1023px) { .st-final-cost__price { font-size: 24px; } }

.st-final-cost__price:not(:last-child) { margin-bottom: 7px; }

.st-final-cost__old-price { position: relative; opacity: 0.4; font-size: 15px; text-transform: uppercase; vertical-align: top; display: inline-block; }

.st-final-cost__old-price:before { position: absolute; content: ""; top: 50%; margin-top: -1px; width: 100%; height: 1px; background-color: #000; }

.st-final-cost__addition { font-size: 16px; }

.st-final-cost__control:not(:first-child) { margin-top: 9px; }

.st-final-cost__info { display: flex; flex: 0 0 40%; min-width: 40%; align-items: center; }

@media only screen and (max-width: 1259px) { .st-final-cost__info { flex: 0 0 25%; min-width: 25%; } }

@media only screen and (max-width: 1023px) { .st-final-cost__info { margin-top: 20px; flex: 0 0 100%; min-width: 100%; order: 2; } }

@media only screen and (max-width: 767px) { .st-final-cost__info { justify-content: center; } }

.st-final-cost__total { flex: 0 0 60%; min-width: 60%; display: flex; flex-wrap: wrap; align-items: center; justify-content: flex-end; }

@media only screen and (max-width: 1259px) { .st-final-cost__total { flex: 0 0 75%; min-width: 75%; } }

@media only screen and (max-width: 1023px) { .st-final-cost__total { oreder: 1; justify-content: space-between; flex: 0 0 100%; min-width: 100%; } }

@media only screen and (max-width: 767px) { .st-final-cost__total { justify-content: center; flex-direction: column; } }

.st-final-cost__devivery-image img { max-height: 20px; display: block; }

.st-final-cost__devivery-content { padding-left: 10px; opacity: 0.8; }

.st-cost-cols { margin-right: 50px; font-size: 0; }

@media only screen and (max-width: 767px) { .st-cost-cols { margin: 0 0 20px; width: 100%; } }

.st-cost-col { position: relative; font-size: 14px; line-height: 1.18; vertical-align: top; display: inline-block; }

@media only screen and (max-width: 767px) { .st-cost-col { text-align: center; display: block; } }

.st-cost-col:not(:first-child) { padding-left: 48px; }

@media only screen and (max-width: 767px) { .st-cost-col:not(:first-child) { padding: 40px 0 0; } }

.st-cost-col:not(:first-child):before { position: absolute; content: ""; top: 0; height: 100%; left: 23px; width: 1px; opacity: 0.5; background-color: #979797; }

@media only screen and (max-width: 767px) { .st-cost-col:not(:first-child):before { top: 19px; left: 0; width: 100%; height: 1px; } }

.st-cost-col__head { opacity: 0.4; }

.st-cost-col__body { font-size: 27px; font-weight: 300; opacity: 0.7; }
